				"html": "<p>We can use <code>if</code> statements in JavaScript to execute code only if the specified condition is met.</p>\n<p>Each <code>if</code> statement requires a <code>boolean</code> condition to evaluate. If the boolean evaluates to <code>true</code>, the statements inside the curly braces will execute. Otherwise, if it evaluates to <code>false</code>, the code will not execute.</p>\n<h2>Example</h2>\n<pre><code>function trueOrFalse(myVal) {\n  if (myVal > 10) {\n     return \"Greater Than\";\n  }\n  return \"Not Greater Than\";\n}\n</code></pre>\n<p>If <code>myVal</code> is greater than <code>10</code>, the function will return <code>\"Greater Than\"</code>. If it is not, the function will return <code>\"Not Greater Than\"</code>. Both <code>return</code> statements will never get executed because as soon as the first one is encountered, the execution exits the function and returns control to where the function was called.</p>",
